.App {
  display: flex;
  background: #ddd;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

h3 {
  text-align: center;
}

.table_container {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-wrap: wrap;
  gap: 20px;
  width: 100%;
}


.messageError {
  margin-top: 20px;
  color: red;
}

/*
@media (min-width: 1500px) {

  .statistik,
  .newZahlen {
     width: 49%;
  }
}
*/
.App>div {
  flex: 1 1;
}


@media (max-width: 1500px) {
  .App {
    flex-direction: column;
  }

  .App>div {
    flex-basis: 100%;
  }
}

@media (max-width: 768px) {
  .table_left {
    width: 100%;
  }

  .statistik,
  .newZahlen {
    width: 100%;
  }
}